export default {
	data() {
		return {
			tabItem: [ // 标签列表
				{
					_id: 1,
					TypeName: "上班"
				},
				{
					_id: 2,
					TypeName: "下班"
				},
			],
			showid: 1, // 选中标签
			cityName: "", // 城市名称
			showTip: false, // 是否显示提示
			gpsRealtimeCity: [], // 定位城市
			showLogin: true, // 显示登录
			headUrl: "../../static/image/icon-bus.png", // 头像
			busList: [{ // 收藏的线路
				lineID: "035180997576", // 线路ID
				lineNo: "804", // 线路
				endSn: "火车站", // 终点
				targetOrder: 1, // 候车站ID
				stationName: "招呼站", // 候车站
				travelTime: 7, // 到达时间（分）
				order: 3, // 还有几站到
				distance: 1.2, // 距离
				state: 0, // -1无车，0正常
			}, {
				lineID: "035180997576", // 线路ID
				lineNo: "804", // 线路
				endSn: "火车站", // 终点
				targetOrder: 1, // 候车站ID
				stationName: "招呼站", // 候车站
				travelTime: 7, // 到达时间（分）
				order: 3, // 还有几站到
				distance: 1.2, // 距离
				state: 0, // -1无车，0正常
			}]
		}
	},
	onLoad: function() {},
	onShow: function() {
		this.cityName = this.$city.name;
		if (this.$wxAvatarUrl != "") {
			this.headUrl = this.$wxAvatarUrl;
			this.showLogin = false;
		}
		// 定位
		this.GetNowLocation();
	},
	methods: {
		// 获取定位
		GetNowLocation: function() {
			var that = this;
			uni.getLocation({
				type: 'wgs84',
				success: res => {
					// 根据定位查询所在城市
					uni.request({
						url: this.$CityServerUrl,
						method: 'GET',
						data: {
							type: "gpsRealtimeCity",
							lat: res.latitude,
							lng: res.longitude,
							gpstype: "wgs",
							s: that.$s,
							v: that.$v,
							src: that.$src,
							userId: that.$userId
						},
						success: res => {
							that.gpsRealtimeCity = res.data.data.gpsRealtimeCity;

							// 如果第一次进入，没有城市ID，则按当前定位显示，否则显示提示
							if (that.$city.id == "") {
								this.$city.id = this.gpsRealtimeCity.cityId;
								this.cityName = this.$city.name = this.gpsRealtimeCity.cityName;
							} else {
								if (that.gpsRealtimeCity.cityId != that.$city.id) {
									that.showTip = true;
								}
							}
						},
						fail: () => {},
						complete: () => {}
					});
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 关闭提示
		btnTipClose_Click: function() {
			this.showTip = false;
		},
		// 点击标签
		btnTab_Click: function() {

		},
		// 点击城市
		btnTipCity_Click: function() {
			this.$city.id = this.gpsRealtimeCity.cityId;
			this.cityName = this.$city.name = this.gpsRealtimeCity.cityName;
			this.showTip = false;
		},
		// 点击搜索
		btnSearch_Click: function() {
			uni.redirectTo({
				url: '../search/search'
			});
		},
		// 点击微信登录
		btnLogin_Click: function(res) {
			console.log(res);
			var userInfo = res.detail.userInfo;
			uni.setStorage({
				key: "wxAvatarUrl",
				data: userInfo.avatarUrl
			})

			this.headUrl = this.$wxAvatarUrl = userInfo.avatarUrl;

			this.showLogin = false;
			uni.showTabBar({
				animation: true
			});
		}
	}
}
